import { computed } from 'vue'
import { useStore } from 'vuex'

// 购物车相关逻辑
const useCommonCartEffect = (shopId) => {
  const store = useStore()
  const { cartList } = store.state
  const changeCartItemInfo = (shopId, productId, productInfo, num) => {
    store.commit('changeCartItemInfo', {
      shopId, productId, productInfo, num
    })
  }
  const productList = computed(() => {
    const productList = store.state.cartList[shopId]?.productList || {}
    const notEmptyProductList = {}
    // 过滤掉(购物车)件数为 0 的商品
    // eslint-disable-next-line prefer-const
    for (let key in productList) {
      const product = productList[key]
      if (product.count > 0) {
        notEmptyProductList[key] = product
      }
    }
    return notEmptyProductList
  })
  const shopName = computed(() => {
    const shopName = store.state.cartList[shopId]?.shopName || ''
    return shopName
  })
  // 计算属性汇总（数量、总价、全选状态）
  const calculations = computed(() => {
    const productList = cartList[shopId]?.productList
    const result = { total: 0, price: 0, allChecked: true }
    if (productList) {
    // eslint-disable-next-line prefer-const
      for (let key in productList) {
        const product = productList[key]
        result.total += product.count
        if (product.check) {
          result.price += (product.count * product.price)
        }
        if (product.count > 0 && !product.check) {
          result.allChecked = false
        }
      }
    }
    result.price = result.price.toFixed(2)
    return result
  })

  return { cartList, productList, shopName, calculations, changeCartItemInfo }
}

export { useCommonCartEffect }
