<template>
    <div
     class="mask"
     v-if="showCart && calculations.total > 0"
     @click="handleCartShowChange"
    />
    <div class="cart">
      <ul class="product" v-if="showCart && calculations.total > 0">
        <div class="product__header">
          <div class="product__header__all">
            <span
             class="product__header__all__icon iconfont"
             v-html="calculations.allChecked ? '&#xe652;' : '&#xe7ae;'"
             @click="() => setCartItemsChecked(shopId)"
            />
            全选
          </div>
          <div
           class="product__header__clear"
           @click="() => clearCartProducts(shopId)"
          >清空购物车</div>
        </div>
        <template
          v-for="item of productList"
          :key="item._id"
        >
          <li class="product__item" v-if="item.count > 0">
            <div
             class="product__item__checked iconfont"
             @click="() => changeCartItemChecked(shopId, item._id)"
             v-html="item.check ? '&#xe652;' : '&#xe7ae;'"
            />
            <img class="product__item__img" :src="item.imgUrl" />
            <div class="product__item__detail">
                <h4 class="product__item__detail__title">{{item.name}}</h4>
                <p class="product__item__detail__price">
                    <span class="product__item__detail__price__yen">&yen;</span>{{item.price}}
                    <span class="product__item__detail__price__origin">&yen;{{item.oldPrice}}</span>
                </p>
            </div>
            <div class="product__item__number">
                <span
                  class="product__item__number__minus iconfont"
                  @click="() => { changeCartItemInfo(shopId, item._id, item, -1) }"
                >&#xe906;</span>
                <span class="product__item__number__font">
                    {{item.count || 0}}
                </span>
                <span
                  class="product__item__number__plus iconfont"
                  @click="() => { changeCartItemInfo(shopId, item._id, item, 1) }"
                >&#xe7ba;</span>
            </div>
          </li>
        </template>
      </ul>
      <div class="cart__check">
        <div class="cart__check__icon">
            <img
              src="http://www.dell-lee.com/imgs/vue3/basket.png"
              class="cart__check__icon__img"
              @click="handleCartShowChange"
            />
            <div class="cart__check__icon__tag">{{calculations.total}}</div>
        </div>
        <div class="cart__check__info">
            总计：<span class="cart__check__info__price">&yen; {{calculations.price}}</span>
        </div>
        <div class="cart__check__btn" v-show="calculations.total > 0">
          <router-link :to="{path: `/orderConfirmation/${shopId}`}">
            去结算
          </router-link>
        </div>
      </div>
    </div>
</template>

<script>
import { ref } from 'vue'
import { useStore } from 'vuex'
import { useRoute } from 'vue-router'
import { useCommonCartEffect } from '../../effects/cartEffects'

// 获取购物车信息逻辑
const useCartEffect = (shopId) => {
  const store = useStore()
  const { productList, calculations, changeCartItemInfo } = useCommonCartEffect(shopId)

  const changeCartItemChecked = (shopId, productId) => {
    store.commit('changeCartItemChecked', { shopId, productId })
  }

  const clearCartProducts = (shopId) => {
    store.commit('clearCartProducts', { shopId })
  }

  const setCartItemsChecked = (shopId) => {
    store.commit('setCartItemsChecked', { shopId })
  }

  return { calculations, productList, changeCartItemInfo, changeCartItemChecked, clearCartProducts, setCartItemsChecked }
}

// 展示隐藏购物车逻辑
const toggleCartEffect = () => {
  const showCart = ref(false)
  const handleCartShowChange = () => {
    showCart.value = !showCart.value
  }
  return { showCart, handleCartShowChange }
}

export default {
  name: 'Cart',
  setup () {
    const route = useRoute()
    const shopId = route.params.id
    const { calculations, productList, changeCartItemInfo, changeCartItemChecked, clearCartProducts, setCartItemsChecked } = useCartEffect(shopId)
    const { showCart, handleCartShowChange } = toggleCartEffect()

    return { calculations, productList, shopId, showCart, changeCartItemInfo, changeCartItemChecked, clearCartProducts, setCartItemsChecked, handleCartShowChange }
  }
}
</script>

<style lang="scss" scoped>
@import '../../style/viriables.scss';
@import '../../style/mixins.scss';
.mask {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background: rgba(0, 0, 0, .5);
  z-index: 1
}
.cart {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  background: $bgColor;
  z-index: 2;
  &__check {
      display: flex;
      border-top: .01rem solid $content-bgColor;
      height: .49rem;
      &__icon {
          position: relative;
          &__img {
              width: .28rem;
              height: .26rem;
              padding: .11rem .24rem .12rem .24rem;
          }
          &__tag {
              position: absolute;
              top: .02rem;
              left: .46rem;
              min-width: .2rem;
              height: .2rem;
              border-radius: .1rem;
              background: $hightLight;
              color: $bgColor;
              line-height: .16rem;
              font-size: .16rem;
              text-align: center;
              transform: scale(.5);
              transform-origin: left center;
          }
      }
      &__info {
          flex: 1;
          line-height: .49rem;
          font-size: .12rem;
          color: $content-fontcolor;
          &__price {
              line-height: .49rem;
              color: $hightLight;
              font-size: .18rem;
          }
      }
      &__btn {
          width: .98rem;
          line-height: .49rem;
          text-align: center;
          font-size: .14rem;
          background: $btns-bgColor;
          a {
            color: $bgColor;
            text-decoration: none;
          }
      }
  }
}
.product {
    margin: 0;
    padding: 0 0;
    list-style: none;
    overflow-y: scroll;
    background: $bgColor;
    &__header {
      display: flex;
      line-height: .52rem;
      background: $bgColor;
      border: .01rem solid $content-bgColor;
      font-size: .14rem;
      color: $content-fontcolor;
      &__all {
        flex: 1;
        margin-left: .18rem;
        &__icon {
          display: inline-block;
          vertical-align: top;
          font-size: .2rem;
          margin-right: .08rem;
          color: $btn-bgColor;
        }
      }
      &__clear {
        text-align: right;
        margin-right: .18rem;
      }
    }
    &__item {
        display: flex;
        position: relative;
        padding: .12rem 0;
        margin: 0 .18rem;
        border-bottom: .01rem solid $content-bgColor;
        &__checked {
          line-height: .46rem;
          font-size: .2rem;
          margin-right: .16rem;
          color: $btn-bgColor;
        }
        &__img {
            width: .46rem;
            height: .46rem;
            margin-right: .16rem;
        }
        &__detail {
            flex: 1;
            height: .46rem;
            overflow: hidden;
            &__title {
                margin: 0;
                line-height: .2rem;
                font-size: .14rem;
                color: $content-fontcolor;
                @include ellipsis;
            }
            &__price {
                line-height: .2rem;
                color: $hightLight;
                font-size: .14rem;
                margin: 0.06rem 0 0 0;
                &__yen {
                    font-size: .1rem;
                }
                &__origin {
                    margin-left: .06rem;
                    font-size: .12rem;
                    color: $light-fontColor;
                    text-decoration: line-through;
                }
            }
        }
        &__number {
            position: absolute;
            right: 0;
            bottom: .26rem;
            height: .2rem;
            &__minus, &__plus {
                font-size: .24rem;
            }
            &__minus {
                color: $medium-fontColor;
                position: relative;
                left: .015rem;
            }
            &__plus {
                color: $btn-bgColor;
            }
            &__font {
                position: relative;
                bottom: .03rem;
                line-height: .16rem;
                font-size: .14rem;
                color: $content-fontcolor;
                margin: 0 .1rem;
            }
        }
    }
}
</style>
