<template>
    <div class="content">
        <ul class="category">
            <li
             :class="{'category__item': true, 'category__item--active': currentTab === item.tab}"
             v-for="item of categories"
             :key="item.name"
             @click="handleTabClick(item.tab)"
            >{{item.name}}</li>
        </ul>
        <ul class="product">
            <li
             class="product__item"
             v-for="item of list"
             :key="item._id"
            >
                <img class="product__item__img" :src="item.imgUrl" />
                <div class="product__item__detail">
                    <h4 class="product__item__detail__title">{{item.name}}</h4>
                    <p class="product__item__detail__sales">月售 {{item.sales}} 件</p>
                    <p class="product__item__detail__price">
                        <span class="product__item__detail__price__yen">&yen;</span>{{item.price}}
                        <span class="product__item__detail__price__origin">&yen;{{item.oldPrice}}</span>
                    </p>
                </div>
                <div class="product__item__number">
                    <span
                     class="product__item__number__minus iconfont"
                     @click="() => { changeCartItem(shopId, item._id, item, -1, shopName) }"
                    >&#xe906;</span>
                    <span class="product__item__number__font">
                        {{getProductCartCount(shopId, item._id)}}
                    </span>
                    <span
                     class="product__item__number__plus iconfont"
                     @click="() => { changeCartItem(shopId, item._id, item, 1, shopName) }"
                    >&#xe7ba;</span>
                </div>
            </li>
        </ul>
    </div>
</template>

<script>
import { reactive, toRefs, ref, watchEffect } from 'vue'
import { useRoute } from 'vue-router'
import { useStore } from 'vuex'
import { get } from '../../utils/request'
import { useCommonCartEffect } from '../../effects/cartEffects'

// 商品分类列表信息
const categories = [
  { name: '全部商品', tab: 'all' },
  { name: '秒杀', tab: 'seckill' },
  { name: '新鲜水果', tab: 'fruit' }
]

// Tab 切换相关的逻辑
const useTabEffect = () => {
  const currentTab = ref(categories[0].tab)
  const handleTabClick = (tab) => {
    currentTab.value = tab
  }
  return { currentTab, handleTabClick }
}

// 列表内容相关的逻辑
const useCurrentListEffect = (currentTab, shopId) => {
  const data = reactive({ list: [] })

  const getContentData = async () => {
    const result = await get(`/api/shop/${shopId}/products`, { tab: currentTab.value })
    if (result?.errno === 0 && result?.data?.length) {
      data.list = result.data
    }
  }
  // 监听请求方法，只要参数一改变，它就执行
  watchEffect(() => getContentData())
  const { list } = toRefs(data)
  return { list }
}

// 购物车相关逻辑
const useCartEffect = () => {
  const store = useStore()
  const { cartList, changeCartItemInfo } = useCommonCartEffect()
  const changeShopName = (shopId, shopName) => {
    store.commit('changeShopName', { shopId, shopName })
  }
  const changeCartItem = (shopId, productId, productInfo, num, shopName) => {
    changeCartItemInfo(shopId, productId, productInfo, num)
    changeShopName(shopId, shopName)
  }
  const getProductCartCount = (shopId, productId) => {
    return cartList?.[shopId]?.productList?.[productId]?.count || 0
  }
  return { cartList, changeCartItem, getProductCartCount }
}

export default {
  name: 'Content',
  props: ['shopName'],
  setup () {
    const route = useRoute()
    const shopId = route.params.id
    const { currentTab, handleTabClick } = useTabEffect()
    const { list } = useCurrentListEffect(currentTab, shopId)
    const { cartList, changeCartItem, getProductCartCount } = useCartEffect()

    return { categories, list, currentTab, cartList, shopId, handleTabClick, changeCartItem, getProductCartCount }
  }
}
</script>

<style lang="scss" scoped>
@import '../../style/viriables.scss';
@import '../../style/mixins.scss';
.content {
    display: flex;
    position: absolute;
    top: 1.5rem;
    left: 0;
    right: 0;
    bottom: .5rem;
}
.category {
    padding: 0 0;
    margin: 0 0;
    list-style: none;
    height: 100%;
    width: .76rem;
    background: $search-bgColor;
    border-radius: .02rem;
    overflow-y: scroll;
    &__item {
        line-height: .4rem;
        text-align: center;
        font-size: .14rem;
        color: $content-fontcolor;
        background: $search-bgColor;
        border-radius: .02rem;
        &--active {
            background: $bgColor;
        }
    }
}
.product {
    flex: 1;
    margin: 0 .18rem .14rem .16rem;
    padding: 0 0;
    list-style: none;
    overflow-y: scroll;
    &__item {
        display: flex;
        position: relative;
        padding: .12rem 0;
        border-bottom: .01rem solid $content-bgColor;
        &__img {
            width: .68rem;
            height: .68rem;
            margin-right: .16rem;
        }
        &__detail {
            flex: 1;
            height: .68rem;
            overflow: hidden;
            &__title {
                margin: 0;
                line-height: .2rem;
                font-size: .14rem;
                color: $content-fontcolor;
                @include ellipsis;
            }
            &__sales {
                margin: .06rem 0;
                line-height: .16rem;
                font-size: .12rem;
                color: $content-fontcolor;
            }
            &__price {
                line-height: .2rem;
                color: $hightLight;
                font-size: .14rem;
                margin: 0;
                &__yen {
                    font-size: .1rem;
                }
                &__origin {
                    margin-left: .06rem;
                    font-size: .12rem;
                    color: $light-fontColor;
                    text-decoration: line-through;
                }
            }
        }
        &__number {
            position: absolute;
            right: 0;
            bottom: .12rem;
            height: .2rem;
            &__minus, &__plus {
                font-size: .24rem;
            }
            &__minus {
                color: $medium-fontColor;
                position: relative;
                left: .015rem;
            }
            &__plus {
                color: $btn-bgColor;
            }
            &__font {
                position: relative;
                bottom: .03rem;
                line-height: .16rem;
                font-size: .14rem;
                color: $content-fontcolor;
                margin: 0 .1rem;
            }
        }
    }
}
</style>
