<template>
  <div class="wrapper">
    <div class="search">
      <div class="search__back iconfont" @click="handleBackClick">&#xe606;</div>
      <div class="search__content">
        <div class="search__content__icon iconfont">&#xe69a;</div>
        <input class="search__content__input" placeholder="请输入商品名称搜索" />
      </div>
    </div>
    <ShopInfo :item="item" :hideBorder="true" v-show="item.imgUrl" />
  </div>
  <Content :shopName="item.name" />
  <Cart />
</template>

<script>
import { reactive, toRefs } from 'vue'
import { useRouter, useRoute } from 'vue-router'
import ShopInfo from '../../components/ShopInfo'
import { get } from '../../utils/request'
import Content from './Content'
import Cart from './Cart'

// 处理获取当前商铺信息
const useShopInfoEffect = () => {
  const route = useRoute()
  const data = reactive({ item: {} })
  const getItemData = async () => {
    console.log(route.params.id)
    const result = await get(`/api/shop/${route.params.id}`)
    if (result?.errno === 0 && result?.data) {
      data.item = result.data
    }
  }
  const { item } = toRefs(data)
  return { item, getItemData }
}

// 处理点击路由返回
const useBackRouterEffect = () => {
  const router = useRouter()
  const handleBackClick = () => {
    router.push({ name: 'Home' })
  }
  return { handleBackClick }
}

export default {
  name: 'Shop',
  components: { ShopInfo, Content, Cart },
  setup () {
    const { item, getItemData } = useShopInfoEffect()
    const { handleBackClick } = useBackRouterEffect()
    getItemData()

    return { item, handleBackClick }
  }
}
</script>

<style lang="scss" scoped>
@import '../../style/viriables.scss';
.wrapper {
  padding: 0 .18rem;
}
.search {
  display: flex;
  margin: .18rem 0 .16rem 0;
  &__back {
    margin-right: .06rem;
    line-height: .32rem;
    color: #B6B6B6;
    font-size: .24rem;
  }
  &__content {
    flex: 1;
    display: flex;
    background: $search-bgColor;
    border-radius: .16rem;
    &__icon {
      margin: 0 .08rem 0 .16rem;
      line-height: .32rem;
      color: $search-fontColor;
    }
    &__input {
      flex: 1;
      padding-right: .2rem;
      line-height: .16rem;
      border: none;
      outline: none;
      background: none;
      color: $content-fontcolor;
      font-size: .14rem;
    }
  }
}
</style>
